
.rows{
  display:grid;
  grid-template-columns: auto auto auto;
  justify-content: center;
  align-content: center;

}
.squares{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 70px;
  font-weight: bolder;
  text-align: center;
  border:solid rgba(2, 1, 2, 0.975) 4px;
  border-radius: 5px;
  height:100px;
  width:100px;
  display:flex;
  justify-content: center;
  align-items: center;
  transition: .5s;
  animation: ease-in infinite ;
  color: rgb(131, 82, 13);
  background-color: rgba(141, 145, 32, 0.543);
}
.squares:hover{
   background-color: darkseagreen;
   box-shadow: 3px 3px 3px  rgba(104, 63, 9, 0.989);
   cursor:pointer;
   animation-name: datal;
    animation-delay: 0;
    animation-iteration-count: infinite;
    animation-duration: 10s;
}
@keyframes datal {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform:rotate(360deg) scaleX(1.1);
    }
    
}
.squares:active{
    color: chocolate;
    box-shadow: inset 5px 5px 5px  rgb(0, 0, 0);
}
